.key.backspace {
    width: 6.4em;
}
.key.tab {
   width: 5em;
}
.key.\| {
    width: 4.6em;
}
.key.caps {
    width: 6em;
}
.key.enter {
    width: 7.6em;
}
.key.shift {
    width: 8em;
}
.\? + .key.shift {
    width: 9.6em;
}
.description {
    margin-top: 2em;
    padding: 1em;
    min-height: 7em;
    text-align: left;
    white-space: pre-wrap;
    width: 789px;
    box-sizing: border-box;
    line-height: 1.5em;
}

.keyboard {
    padding: 1em 1em 1.5em 1em;
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-direction: column;
    border-radius: 6px;
}

.buttonList {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-direction: row;
}

.key {
    display: flex;
    flex-direction: column;
    width: 3.2em;
    height: 3.2em;
    margin: .3em;
    border-radius: 5px;
    cursor: pointer;
    font-size: small;
}

.key div {
    box-sizing: border-box;
    padding: 5px;
    height: 50%;
    text-align: left;
}

.key div:last-child {
    text-align: right;
}

.App {
    text-align: center;
    font-family: Roboto, sans-serif;
    font-size: small;
}
.App-main {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.toggle-wrapper {
    display: flex;
    flex-direction: row;
    margin: 20px 0;
}

h3 {
  font-weight: bold;
  font-size: 1.5em;
  margin-bottom: .4em;
}

.App-main {
    color: #ffffff;
    background: -webkit-linear-gradient(215deg, #c5c1ff, #fcd0d6);
    background: linear-gradient(235deg, #c5c1ff, #fcd0d6);
}
.keyboard {
    background-image: -webkit-linear-gradient(45deg, #F8F9FD, #F0F2FA);
    background-image: linear-gradient(45deg, #F8F9FD, #F0F2FA);
    box-shadow: 0 0 50px 0 rgba(48, 50, 58, 0.26);
}
.key {
    background: #43454D;
    box-shadow: 0 0 16px 0 rgba(110, 119, 156, 0.26);
    border: 1px solid #383941;
}
.key div:hover {
    color: #94FFEF;
}

.key div.active {
    color: #94FFEF;
}

.description {
    color: black;
    background: white;
}

.toggle-wrapper {
    color: #000;
}

.dark.App-main {
    color: #ffffff;
    background: #2b2b2f;
}
.dark .keyboard {
    background-blend-mode: normal, overlay;
    background-image: -webkit-linear-gradient(162deg, #3F414A, #4A4C56);
    background-image: linear-gradient(288deg, #3F414A, #4A4C56);
    box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.26);
}
.dark .key {
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.26);
    background: -webkit-linear-gradient(135deg, #282a2f, #222327);
    background: linear-gradient(315deg, #282a2f, #222327);
    border: 1px solid #282a2f;
}
.dark .key div:hover {
    color: #94FFEF;
}

.dark .key div.active {
    color: #94FFEF;
}

.dark .description {
    color: #fff;
    background-color: #474852
}

.dark .toggle-wrapper {
    color: #fff;
}

